// src/components/product/ProductCard.js
import React from "react";
import { Link } from "react-router-dom";
import { Star, Clock, Heart, Plus } from "lucide-react";
import { useCart } from "../../context/CartContext";

export default function ProductCard({ product }) {
  const { addToCart, wishlist, toggleWishlist } = useCart();

  if (!product) return null;

  const wished = Array.isArray(wishlist) && wishlist.includes(product.id);

  const handleAdd = () => addToCart?.(product);
  const handleToggleWish = (e) => {
    e?.preventDefault?.();
    e?.stopPropagation?.();
    toggleWishlist?.(product.id);
  };

  return (
    <div
      className="bg-white rounded-xl shadow-lg overflow-hidden hover:shadow-xl transition-all duration-300 transform hover:-translate-y-2"
      style={{
        background: "#fff",
        borderRadius: "0.75rem",
        boxShadow: "0 10px 15px -3px rgba(0,0,0,0.1)",
        overflow: "hidden",
        transition: "all 0.3s ease",
        transform: "translateY(0)",
        cursor: "pointer",
      }}
    >
      <div style={{ position: "relative" }}>
        <Link to={`/product/${product.id}`} className="block">
          <img
            src={product.image}
            alt={product.name}
            loading="lazy"
            onError={(e) => {
              if (e.currentTarget.dataset.fbk) return; // avoid loop
              e.currentTarget.dataset.fbk = "1";
              e.currentTarget.src = "https://placehold.co/600x400?text=No+Image";
            }}
            style={{
              width: "100%",
              height: "200px",
              objectFit: "cover",
              display: "block",
            }}
          />
        </Link>

        {/* Badges */}
        <div
          style={{
            position: "absolute",
            top: "1rem",
            left: "1rem",
            display: "flex",
            flexDirection: "column",
            gap: "0.5rem",
          }}
        >
          {product.isBestseller && (
            <span
              style={{
                background: "#ef4444",
                color: "#fff",
                fontSize: "0.75rem",
                fontWeight: "800",
                padding: "0.25rem 0.5rem",
                borderRadius: "9999px",
              }}
            >
              üî• BESTSELLER
            </span>
          )}
          {product.discount > 0 && (
            <span
              style={{
                background: "#16a34a",
                color: "#fff",
                fontSize: "0.75rem",
                fontWeight: "800",
                padding: "0.25rem 0.5rem",
                borderRadius: "9999px",
              }}
            >
              {product.discount}% OFF
            </span>
          )}
        </div>

        {/* Wishlist toggle (doesn't open product) */}
        <button
          onClick={handleToggleWish}
          style={{
            position: "absolute",
            top: "1rem",
            right: "1rem",
            padding: "0.5rem",
            borderRadius: "9999px",
            border: "none",
            background: wished ? "#ef4444" : "rgba(255,255,255,0.9)",
            color: wished ? "#fff" : "#374151",
            cursor: "pointer",
          }}
          aria-pressed={wished}
          aria-label={wished ? "Remove from wishlist" : "Add to wishlist"}
          title={wished ? "Remove from wishlist" : "Add to wishlist"}
        >
          <Heart size={16} fill={wished ? "currentColor" : "none"} />
        </button>
      </div>

      <div style={{ padding: "1.5rem" }}>
        {/* Title + rating */}
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "flex-start",
            marginBottom: "0.75rem",
          }}
        >
          <Link
            to={`/product/${product.id}`}
            style={{
              fontSize: "1.25rem",
              fontWeight: 700,
              color: "#111827",
              textDecoration: "none",
            }}
          >
            {product.name}
          </Link>

          <div style={{ display: "flex", alignItems: "center", gap: "0.25rem" }}>
            <Star style={{ color: "#fbbf24" }} size={16} fill="currentColor" />
            <span style={{ fontSize: "0.875rem", fontWeight: 600 }}>
              {product.rating}
            </span>
            <span style={{ fontSize: "0.75rem", color: "#6b7280" }}>
              ({product.reviewCount})
            </span>
          </div>
        </div>

        {/* Description */}
        <p
          style={{
            color: "#6b7280",
            marginBottom: "1rem",
            fontSize: "0.875rem",
          }}
        >
          {product.description}
        </p>

        {/* Price + Add */}
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
            marginBottom: "1rem",
          }}
        >
          <div style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
            <span
              style={{ fontSize: "1.5rem", fontWeight: 900, color: "#ea580c" }}
            >
              ‚Çπ{product.price}
            </span>
            {product.originalPrice > product.price && (
              <span
                style={{
                  fontSize: "0.875rem",
                  color: "#9ca3af",
                  textDecoration: "line-through",
                }}
              >
                ‚Çπ{product.originalPrice}
              </span>
            )}
          </div>

          <button
            onClick={handleAdd}
            style={{
              background: "linear-gradient(90deg, #f97316, #ef4444)",
              color: "#fff",
              padding: "0.5rem 1rem",
              borderRadius: "9999px",
              border: "none",
              fontWeight: 600,
              cursor: "pointer",
              display: "flex",
              alignItems: "center",
              gap: "0.5rem",
              transition: "transform 0.2s ease",
            }}
            onMouseEnter={(e) =>
              (e.currentTarget.style.transform = "scale(1.05)")
            }
            onMouseLeave={(e) =>
              (e.currentTarget.style.transform = "scale(1)")
            }
          >
            <Plus size={16} />
            <span>Add</span>
          </button>
        </div>

        {/* Meta row */}
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
            fontSize: "0.875rem",
            color: "#6b7280",
            paddingTop: "1rem",
            borderTop: "1px solid #f3f4f6",
          }}
        >
          <div style={{ display: "flex", alignItems: "center", gap: "0.25rem" }}>
            <Clock size={14} />
            <span>{product.prepTime}</span>
          </div>
          <div
            style={{
              padding: "0.25rem 0.5rem",
              borderRadius: "9999px",
              fontSize: "0.75rem",
              fontWeight: 600,
              background:
                product.spiceLevel === "Mild"
                  ? "#dcfce7"
                  : product.spiceLevel === "Medium"
                  ? "#fef3c7"
                  : "#fee2e2",
              color:
                product.spiceLevel === "Mild"
                  ? "#166534"
                  : product.spiceLevel === "Medium"
                  ? "#92400e"
                  : "#991b1b",
            }}
          >
            üå∂Ô∏è {product.spiceLevel}
          </div>
        </div>
      </div>
    </div>
  );
}
