import React, { useMemo, useState } from "react";
import ProductCard from "./ProductCard";
import { getCategoryCounts } from "../../services/productService";
import "./productlist.css";

export default function ProductList({ items = [] }) {
  const [selectedCategory, setSelectedCategory] = useState("all");
  const [searchQuery, setSearchQuery] = useState("");
  const [sortBy, setSortBy] = useState("name");

  const categories = getCategoryCounts();

  const filtered = useMemo(() => {
    const q = searchQuery.trim().toLowerCase();
    const byCat = items.filter(
      (f) =>
        (selectedCategory === "all" || f.category === selectedCategory) &&
        (!q ||
          f.name.toLowerCase().includes(q) ||
          f.description.toLowerCase().includes(q))
    );

    return byCat.sort((a, b) => {
      switch (sortBy) {
        case "price-low":
          return a.price - b.price;
        case "price-high":
          return b.price - a.price;
        case "rating":
          return b.rating - a.rating;
        case "popular":
          return b.reviewCount - a.reviewCount;
        default:
          return a.name.localeCompare(b.name);
      }
    });
  }, [items, selectedCategory, searchQuery, sortBy]);

  return (
    <div className="plist">
      {/* Controls */}
      <div className="plist__controls">
        <div className="plist__cats" role="tablist" aria-label="Categories">
          {categories.map((c) => (
            <button
              key={c.id}
              type="button"
              role="tab"
              aria-selected={selectedCategory === c.id}
              className={`cat-pill ${selectedCategory === c.id ? "is-active" : ""}`}
              onClick={() => setSelectedCategory(c.id)}
            >
              <span className="cat-pill__emoji" aria-hidden>
                {c.emoji}
              </span>
              <span className="cat-pill__label">{c.name}</span>
              <span className="cat-pill__count">{c.count}</span>
            </button>
          ))}
        </div>

        <div className="plist__searchsort">
          <input
            className="plist__search"
            placeholder="Search food..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />

          <select
            className="plist__sort"
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            aria-label="Sort products"
          >
            <option value="name">Sort by Name</option>
            <option value="price-low">Price: Low to High</option>
            <option value="price-high">Price: High to Low</option>
            <option value="rating">Highest Rated</option>
            <option value="popular">Most Popular</option>
          </select>
        </div>
      </div>

      {/* Result meta */}
      <p className="plist__meta">
        Showing <b>{filtered.length}</b> of <b>{items.length}</b> items
        {searchQuery ? <> for ‚Äú{searchQuery}‚Äù</> : null}
      </p>

      {/* Grid */}
      <div className="product-grid">
        {filtered.map((p) => (
          <ProductCard key={p.id} product={p} />
        ))}
      </div>

      {/* Empty state */}
      {filtered.length === 0 && (
        <div className="plist__empty">
          <div className="plist__emoji">üîç</div>
          <h3>No items found</h3>
          <p>Try adjusting your search or filters</p>
          <button
            className="plist__reset"
            onClick={() => {
              setSearchQuery("");
              setSelectedCategory("all");
              setSortBy("name");
            }}
          >
            Reset Filters
          </button>
        </div>
      )}
    </div>
  );
}
